import React, { Component } from 'react';
import Header from './components/header';
// import SideBar from './components/sideBar';
import Content from './components/content';
import SecNav from './components/secNav'
import AdminFooter from './components/footer';
import AdminLogIn from './adminLogin';
import axios from 'axios';
import {Link} from "react-router-dom";
class AdminPage extends Component {
  state = {
    List: [],
    session: 0,
    x: "yes",
    y: "yes" //here will be no during implementation
  };
  constructor(props) {
    super(props);
    // alert("All the admin pages will not be accessible after implementation without authentication");
    // this.state = { newsList: [] };
    axios.get('https://fyp-4682-saylani-batch2.herokuapp.com/session')
      .then(response => {
        this.setState({ List: response.data });
        console.log(response.data);
        if(this.state.List.length != 0){
          // alert("Passed");
          this.state.session = this.state.List[0]
           
            console.log(this.state.session.sessionDB);
            let localSession = Number( localStorage.getItem("session"));
            console.log(localSession);
            if(this.state.session.sessionDB == localSession){
              this.state.y = "yes";
            }
            else{
              this.state.y = "no";
              // window.location.assign(window.location.href +" admin")
              
            }
          }
          else{
            // window.location.assign(window.location.href +" admin")
            this.state.y = "no";
            
          }
          
        })
        .catch(function (error) {
          console.log(error);
        })
        
      }
      
      
      

      
      
      render() {
        if (this.state.x == this.state.y) {
          return (<div className="ctmBgTeal ">
        <Header />

        <div className="welcomeAdmin">
          <br />
          Welcome Admin!
      <br />
        </div>
        <SecNav />
        <br />
        <div className="">
          <Content />
        </div>
        <br />
        <AdminFooter />
      </div>)
    }
    else {
      return (
        // <AdminLogIn />
        <div className="ctmBgTeal">
        <div className="co">Sorry! Login required.. To access this page! </div>
        <Link to="/admin">Login</Link>
        
        </div>
      )
    }
  }
}

export default AdminPage;